angular.module('app.accounts.factory', [])
    .factory('AccountsFactory', ['MessagesFactory', 'urlMap', '$resource', function (MessagesFactory, urlMap, $resource) {

        var _messages = {
            errorOnGetAccountsList: 'Cannot get accounts',
            errorOnAddAccount: 'Cannot add account',
            errorOnUpdateAccounts: 'Cannot update accounts',
            errorOnDeleteAccount: 'Cannot remove account',
            successOnAddAccount: '  ',
            successOnUpdateAccount: '  ',
            successOnDeleteAccount: '  '
        };

        var _accountsApi = {
            accounts: $resource(urlMap['accounts.get_accounts'],
                {},
                {
                    get: {method: 'GET'}
                }
            ),
            deleteAccounts: $resource(urlMap['accounts.delete_accounts'],
                {},
                {
                    remove: {method: 'DELETE'}
                }
            ),
            addAccounts: $resource(urlMap['accounts.add_account'],
                {},
                {
                    add: {method: 'POST'}
                }
            ),
            updateAccounts: $resource(urlMap['accounts.update_account'],
                {account_id: '@account_id'},
                {
                    save: {method: 'PUT'}
                }
            )
        };

        var exports = {
            getAccountsList: function (page, limit, orderBy, filter) {
                var data = {
                    page: page,
                    limit: limit,
                    order_by: orderBy
                };

                angular.forEach(filter, function (value, key) {
                    if (value) {
                        data[key] = value;
                    }
                });

                return _accountsApi.accounts.get(data, function (data) {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnGetAccountsList);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            getAccountsListByIds: function (ids) {
                if (!ids) return MessagesFactory.throwInvalidParamsError();

                return _accountsApi.accounts.get({
                    ids: ids
                }, function (data) {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnGetAccountsList);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            addAccount: function (account) {
                if (!account) return MessagesFactory.throwInvalidParamsError();

                return _accountsApi.addAccounts.add(account, function () {
                    MessagesFactory.successMsg(_messages.successOnAddAccount);
                }, function (response) {
                    MessagesFactory.throwDeveloperError(_messages.errorOnAddAccount);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            removeAccounts: function (accountsIds) {
                if (!accountsIds) return MessagesFactory.throwInvalidParamsError();

                return _accountsApi.deleteAccounts.remove({ids: accountsIds}, function () {
                    MessagesFactory.successMsg(_messages.successOnDeleteAccount);
                }, function (response) {
                    MessagesFactory.throwDeveloperError(_messages.errorOnDeleteAccount);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            updateAccount: function (account) {
                if (!account) return MessagesFactory.throwInvalidParamsError();

                return _accountsApi.updateAccounts.save({account_id: account.id}, account, function () {
                    MessagesFactory.successMsg(_messages.successOnUpdateAccount);
                }, function (response) {
                    MessagesFactory.throwDeveloperError(_messages.errorOnUpdateAccounts);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            }
        };

        return exports;
    }])
;
angular.module('app.analytics.factory', [])

    .factory('AnalyticsFactory', ['MessagesFactory', 'urlMap', '$resource', function (MessagesFactory, urlMap, $resource) {

        var _messages = {
            errorOnGetAnalyticsData: 'Cannot get analytics data',
            errorSaveQuery: 'Cannot save analytics query',
            errorLoadSavedQueries: 'Cannot get saved queries list'
        };

        var _analyticsApi = {
            analytics: $resource(urlMap['analytics.get_cards_freq'],
                {},
                {
                    getData: {method: 'GET'}
                }),
            query: $resource(urlMap['analytics.save_query'],
                {},
                {
                    save: {method: 'POST'}
                }),
            queryList: $resource(urlMap['analytics.get_queries'],
                {},
                {
                    getList: {method: 'GET'}
                })
        };

        var exports = {
            data: [],
            getAnalyticsData: function (startDate, endDate, queriesList, macrosIdsList) {
                if (!startDate || !endDate || !queriesList) return MessagesFactory.throwInvalidParamsError();

                var queries = JSON.stringify(queriesList);
                var macrosIds = (!!macrosIdsList) ? macrosIdsList.toString() : null;

                return _analyticsApi.analytics.getData({
                    start_date: startDate,
                    end_date: endDate,
                    queries: queries,
                    macros_ids: macrosIds
                }, function (data) {
                    exports.data = data.results;
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnGetAnalyticsData);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            saveQuery: function (name, params) {
                return _analyticsApi.query.save({name: name, params: params}, function () {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorSaveQuery);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            getSavedQueriesList: function () {
                return _analyticsApi.queryList.getList({}, function () {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorLoadSavedQueries);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            }
        };

        return exports;

    }])

;

