'use strict';

angular.module('app.card.factory', [])
    .factory('CardFactory', ['MessagesFactory', 'urlMap', '$resource', '$q', function (MessagesFactory, urlMap, $resource, $q) {

        var _messages = {
            errorOnGetCards: 'Cannot get cards',
            errorOnAddCard: 'Cannot add card',
            errorOnDeleteCards: 'Cannot delete cards',
            errorOnUpdateCards: 'Cannot update cards',
            successOnAddCard: '  ',
            successOnDeleteCard: '  ',
            successOnUpdateCard: '  '
        };

        function _toJson(data) {
            return angular.toJson(data, true);
        }

        var _cardsApi = {
            cards: $resource(urlMap['cards.get_cards'],
                {},
                {
                    get: {method: 'GET'}
                }
            ),
            addCard: $resource(urlMap['cards.add_card'],
                {},
                {
                    add: {method: 'POST'}
                }
            ),
            card: $resource(urlMap['cards.update_card'],
                {card_id: '@card_id'},
                {
                    update: {method: 'PUT'},
                    remove: {method: 'DELETE'}
                }
            )
        };

        var exports = {
            getCardsList: function (page, limit, orderBy, filter) {
                var data = {
                    page: page,
                    limit: limit,
                    order_by: orderBy
                };

                angular.forEach(filter, function (value, key) {
                    if (value) {
                        data[key] = value;
                    }
                });

                return _cardsApi.cards.get(data, function (data) {
                    // FIXME: maybe it's be better to do this on a backend
                    if (data.results) {
                        for (var i = 0; i < data.results.length; i++) {
                            data.results[i].raw_data = data.results[i].data;
                            data.results[i].data = _toJson(data.results[i].data);
                        }
                    }
                }, function (response) {
                    MessagesFactory.throwDeveloperError(_messages.errorOnGetCards);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            getCardsById: function (ids) {
                if (!ids) return MessagesFactory.throwInvalidParamsError();

                return _cardsApi.cards.get({
                    ids: ids
                }, function (data) {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnGetCards);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            addCard: function (data, macrosId) {
                return _cardsApi.addCard.add({data: data, macros_id: macrosId}, function () {
                    if (!data || !macrosId) return MessagesFactory.throwInvalidParamsError();

                    MessagesFactory.successMsg(_messages.successOnAddCard);
                }, function (response) {
                    MessagesFactory.throwDeveloperError(_messages.errorOnAddCard);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            removeCards: function (cardsIds) {
                if (!cardsIds) return MessagesFactory.throwInvalidParamsError();

                if (!angular.isArray(cardsIds)) cardsIds = [cardsIds];

                if (cardsIds.length > 1) {
                    return _removeMassiveCards(cardsIds);
                } else {
                    return _removeSingleCard(cardsIds[0]);
                }
            },
            updateCard: function (card) {
                if (!card) return MessagesFactory.throwInvalidParamsError();

                return _cardsApi.card.update({card_id: card.id, data: card.data}, function () {
                    MessagesFactory.successMsg(_messages.successOnUpdateCard);
                }, function (response) {
                    MessagesFactory.throwDeveloperError(_messages.errorOnUpdateCards);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            }
        };

        function _removeMassiveCards(cardIds) {
            var promises = [];

            for (var i = 0; i < cardIds.length; i++) {
                promises.push(_cardsApi.card.remove({card_id: cardIds[i], "delete": true}).$promise);
            }

            return $q.all(promises);
        }

        function _removeSingleCard(cardId) {
            return _cardsApi.card.remove({card_id: cardId}, function () {
                MessagesFactory.successMsg(_messages.successOnDeleteCard);
            }, function (response) {
                MessagesFactory.throwDeveloperError(_messages.errorOnDeleteCards);
                MessagesFactory.statusErrorMsg(response.status);
            }).$promise;
        }

        return exports;
    }])
;
