angular.module('app.launch.factory', [])

    .factory('LaunchFactory', ['MessagesFactory', 'urlMap', '$resource', 'MacrosFactory', '$q', function (MessagesFactory, urlMap, $resource, MacrosFactory, $q) {

        var _messages = {
            errorOnGetScanList: 'Cannot get launch list',
            errorOnAddScan: 'Cannot add new launch',
            errorOnStopScan: 'Cannot stop launch',
            errorOnUpdateScan: 'Cannot update launch',
            errorOnRemoveScan: 'Cannot remove launch'
        };

        var _launchApi = {
            newLaunch: $resource(urlMap['launches.add_launch'],
                {},
                {
                    add: {method: 'POST'}
                }),
            launch: $resource(urlMap['launches.update_launch'],
                {launch_id: '@launch_id'},
                {
                    update: {method: 'PUT'}
                }),
            launchList: $resource(urlMap['launches.get_launches'],
                {},
                {
                    getList: {method: 'GET'}
                }),
            launchMassiveDelete: $resource(urlMap['launches.launch_mass_delete'],
                {ids: '@ids'},
                {
                    remove: {method: 'DELETE'}
                }),
            launchMassiveEnable: $resource(urlMap['launches.launch_mass_enable'],
                {},
                {
                    update: {method: 'PUT'}
                })
        };

        var exports = {
            addLaunch: function (macrosIds, settings) {
                if (!macrosIds || !settings) return MessagesFactory.throwInvalidParamsError();
                if (!angular.isArray(macrosIds)) macrosIds = [macrosIds];

                if (macrosIds.length > 1) {
                    return _addMassiveLaunch(macrosIds, settings);
                } else {
                    return _addSingleLaunch(macrosIds[0], settings);
                }
            },
            updateLaunch: function (launch) {
                if (!launch) return MessagesFactory.throwInvalidParamsError();

                return _launchApi.launch.update({launch_id: launch.id}, launch, function (data) {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnUpdateScan);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            removeLaunch: function (launchIds) {
                if (!launchIds) return MessagesFactory.throwInvalidParamsError();
                if (!angular.isArray(launchIds)) launchIds = [launchIds];

                return _launchApi.launchMassiveDelete.remove({ids: launchIds.toString()}, function (data) {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnRemoveScan);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            getMacrosForLaunchList: function (launchList) {
                var macrosIds = [];

                for (var i = 0; i < launchList.length; i++) {
                    macrosIds.push(launchList[i].macros_id);
                }

                return MacrosFactory.getMacrosListByIds(macrosIds.toString());
            },
            assignLinkedMacrosToLaunchList: function (launchList, callback) {
                if (!angular.isArray(launchList)) launchList = [launchList];

                return exports.getMacrosForLaunchList(launchList).then(function (data) {
                    var macrosList = data.results;

                    for (var i = 0; i < launchList.length; i++) {
                        var launch = launchList[i];
                        for (var j = 0; j < macrosList.length; j++) {
                            var macros = macrosList[j];
                            if (+launch.macros_id === +macros.id) {
                                launch.macros = macros;
                            }
                        }
                    }

                    if (callback) {
                        callback(launchList);
                    }
                });
            },
            getLaunchList: function (page, limit, orderBy, filter) {
                var data = {
                    page: page,
                    limit: limit,
                    order_by: orderBy
                };

                angular.forEach(filter, function (value, key) {
                    if (value) {
                        data[key] = value;
                    }
                });

                return _launchApi.launchList.getList(data, function (data) {
                    exports.launchList = data.results;
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnGetScanList);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            getLaunchListByIds: function (ids) {
                if (!ids) return MessagesFactory.throwInvalidParamsError();

                return _launchApi.launchList.getList({
                    ids: ids
                }, function (data) {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnGetScanList);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            },
            setLaunchEnableMassive: function (launchIds, isEnableAction) {
                if (!launchIds) return MessagesFactory.throwInvalidParamsError();

                return _launchApi.launchMassiveEnable.update(null, {
                    ids: launchIds,
                    is_enabled: isEnableAction
                }, function (data) {
                    //
                }, function (response) {
                    MessagesFactory.throwDeveloperWarn(_messages.errorOnUpdateScan);
                    MessagesFactory.statusErrorMsg(response.status);
                }).$promise;
            }
        };

        function _addMassiveLaunch(macrosIds, settings) {
            var promises = [];
            var launch;

            var params = JSON.parse(JSON.stringify(settings.params));
            delete settings.params;

            for (var i = 0; i < macrosIds.length; i++) {
                launch = JSON.parse(JSON.stringify(settings));
                launch.macros_id = macrosIds[i];

                for (var j = 0; j < params.length; j++) {
                    var param = params[j];
                    if (launch.macros_id === param.id) {
                        launch.params = param.placeholders;
                    }
                }

                promises.push(_launchApi.newLaunch.add(launch).$promise);
            }

            return $q.all(promises);
        }

        function _addSingleLaunch(macrosId, settings) {
            settings.macros_id = macrosId;

            if (settings.params) {
                var params = JSON.parse(JSON.stringify(settings.params));
                delete settings.params;
                if (params) {
                    settings.params = params[0].placeholders;
                }
            }

            return _launchApi.newLaunch.add(settings, function (data) {
                //
            }, function (response) {
                MessagesFactory.throwDeveloperWarn(_messages.errorOnAddScan);
                MessagesFactory.statusErrorMsg(response.status);
            }).$promise;
        }

        return exports;
    }])

;

