'use strict';

angular.module('app.messages', ['toaster'])

    .constant('MESSAGES', {
        invalidParams: 'invalid params'
    })

    .factory('MessagesFactory', ['toaster', 'HTTP_STATUS', 'MESSAGES', function (toaster, HTTP_STATUS, MESSAGES) {

        var _messages = {
            badRequest: ' ',
            unauthorized: ' ',
            internalServerError: '  ',
            unknownError: ' ,      '
        };

        var _messageTypes = {
            success: 'success',
            error: 'error',
            info: 'info',
            warning: 'warning',
            wait: 'wait'
        };

        var _messagesTitles = {
            error: 'Error'
        };

        function getDefaultMessage(status) {
            if (status === HTTP_STATUS.BAD_REQUEST) {
                return _messages.badRequest;
            } else if (status === HTTP_STATUS.UNAUTHORIZED) {
                return _messages.unauthorized;
            } else if (status === HTTP_STATUS.INTERNAL_SERVER_ERROR) {
                return _messages.internalServerError;
            } else if (status === HTTP_STATUS.NOT_FOUND) {
                return ' ';
            } else {
                return ' : ' + status;
            }
        }

        var exports = {
            successMsg: function (message, title) {
                if (!message) return exports.throwInvalidParamsError();

                title = title || null;

                toaster.pop(_messageTypes.success, title, message);
            },
            errorMsg: function (message, title) {
                if (!message) return exports.throwInvalidParamsError();

                title = title || _messagesTitles.error;

                toaster.pop(_messageTypes.error, title, message);
            },
            infoMsg: function (message, title) {
                if (!message) return exports.throwInvalidParamsError();

                toaster.pop(_messageTypes.info, title, message);
            },
            statusErrorMsg: function (status) {
                if (!status) return exports.throwInvalidParamsError();

                var message = getDefaultMessage(status);
                var title = _messagesTitles.error;

                toaster.pop(_messageTypes.error, title, message);
            },
            throwDeveloperError: function (message) {
                return console.error(message);
            },
            throwDeveloperWarn: function (message) {
                return console.warn(message);
            },
            throwInvalidParamsError: function () {
                return exports.throwDeveloperError(MESSAGES.invalidParams);
            }
        };

        return exports;
    }])
;
'use strict';

angular.module('app.ng-table-utils', ['ngTable'])

    .factory('NgTableUtilsFactory', ['NgTableParams', 'MessagesFactory', function (NgTableParams, MessagesFactory) {

        var defaultPage = 1;
        var defaultCount = 25;

        var _sortingDirection = {
            asc: '',
            desc: '-'
        };

        function _getSorting(sorting) {
            var field = Object.keys(sorting)[0];
            var direction = sorting[field] === 'asc' ? _sortingDirection.asc : _sortingDirection.desc;
            return direction + field;
        }

        //TODO (S.Panfilov) remove from factories lists, schedulers, dones and totals for reload table
        var exports = {
            tableParams: null,
            getDefaultParams: function (settings, sorting) {
                return {
                    defaultPage: 1,
                    defaultCount: 25,
                    page: (settings && settings.page) ? settings.page : defaultPage,
                    count: (settings && settings.count) ? settings.count : defaultCount,
                    sorting: sorting
                };
            },
            initNgTableParams: function (settings, sorting, promisedCallback) {
                var defTableParams = exports.getDefaultParams(settings, sorting);

                exports.tableParams = new NgTableParams({
                    page: defTableParams.page,
                    count: defTableParams.count,
                    sorting: defTableParams.sorting
                }, {
                    total: 0,
                    getData: function ($defer, params) {
                        var page = params.page() || defTableParams.page;
                        var limit = params.count() || defTableParams.count;
                        var sorting = _getSorting(params.sorting() || defTableParams.sorting);

                        if (!promisedCallback) return MessagesFactory.throwInvalidParamsError();

                        promisedCallback(page, limit, sorting).then(function (data) {
                            params.total(data.total);
                            return $defer.resolve(data.results);
                        });
                    }
                });

                return exports.tableParams;
            },
            setDefaultSorting: function (sorting) {
                return exports.tableParams.sorting(sorting);
            },
            reload: function () {
                if (!exports.tableParams) return MessagesFactory.throwInvalidParamsError();
                exports.tableParams.reload();
            }
        };

        return exports;
    }])
;
