angular.module('app.pages.accounts.modals.add_account', [])
    .controller('AddAccountModalCtrl', ['$modalInstance', '$scope', '$state', 'AccountsFactory', 'ScriptFactory', 'NgTableUtilsFactory', function ($modalInstance, $scope, $state, AccountsFactory, ScriptFactory, NgTableUtilsFactory) {

        $scope.account = {};
        $scope.isValidScript = false;

        $scope.editorOptions = {
            lineWrapping: true,
            lineNumbers: true,
            mode: {name: "javascript", json: true}
        };

        function prepareForSave(account) {
            account.args = ScriptFactory.removeFormatting(account.dataPreview);

            var pack = JSON.parse(JSON.stringify(account));
            delete pack.dataPreview;

            return pack;
        }

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        $scope.$watch('account.dataPreview', function (value) {
                if (!value) {
                    $scope.isValidScript = false;
                    return;
                }

                ScriptFactory.isValidScript(value).then(function (data) {
                    $scope.isValidScript = data.isValid;
                });
            }, true
        );


        $scope.save = function () {
            var pack = prepareForSave($scope.account);

            return AccountsFactory.addAccount(pack).then(function () {
                $modalInstance.close();
                NgTableUtilsFactory.reload();
            });
        };

        (function init() {
            //
        })();

    }])
;
