angular.module('app.pages.accounts.modals.edit_account', [])

    .controller('EditAccountModalCtrl', ['$scope', '$modalInstance', 'AccountsFactory', 'accountData', '$timeout', 'ScriptFactory', 'NgTableUtilsFactory', function ($scope, $modalInstance, AccountsFactory, accountData, $timeout, ScriptFactory, NgTableUtilsFactory) {

        $scope.account = {};
        $scope.isValidScript = false;
        $scope.isError = false;

        $scope.editorOptions = {
            lineWrapping: true,
            lineNumbers: true,
            mode: {name: "javascript", json: true}
        };

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        function prepareAccountOnGet(account) {
            if (account && account.args) {
                account.dataPreview = ScriptFactory.stringify(account.args, true);
            }

            return account;
        }

        function prepareForSave(account) {
            account.args = ScriptFactory.removeFormatting(account.dataPreview);

            var pack = JSON.parse(JSON.stringify(account));
            delete pack.dataPreview;

            return pack;
        }

        $scope.update = function () {
            var pack = prepareForSave($scope.account);

            return AccountsFactory.updateAccount(pack).then(function () {
                NgTableUtilsFactory.reload();
                $modalInstance.close();
            });
        };

        function refreshCodeMirrorOnLoad() {
            $timeout(function () {
                $scope.isLoaded = !$scope.isLoaded;
            });
        }

        $scope.$watch('account.dataPreview', function (value) {
                if (!value) {
                    $scope.isValidScript = false;
                    return;
                }

                ScriptFactory.isValidScript(value).then(function (data) {
                    $scope.isValidScript = data.isValid;
                });
            }, true
        );


        function isLoadError() {
            if (accountData.results.length === 0 || accountData.results.length > 1) return true;

            return false;
        }

        (function _init() {
            var account = accountData.results[0]; //we expect only one account
            $scope.account = prepareAccountOnGet(account);
            $scope.isError = isLoadError();
            refreshCodeMirrorOnLoad();
        })();
    }])
;
