angular.module('app.pages.analytics.modals.load_query', [])

    .controller('LoadQueryModalCtrl', ['$scope', '$modalInstance', '$location', 'queriesListData', '$state', 'MessagesFactory', function ($scope, $modalInstance, $location, queriesListData, $state, MessagesFactory) {

        var _messages = {
            unknownDateFormatError: 'unknown date format'
        };

        $scope.queriesList = [];

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        function _parseDate(dateStr) {
            var expectFormat = "dd.MM.YYYY";

            if (dateStr.length !== expectFormat.length) return MessagesFactory.throwDeveloperError(_messages.unknownDateFormatError);

            var year = dateStr.substr(dateStr.lastIndexOf('.') + 1);
            var month = dateStr.substr(dateStr.indexOf('.') + 1, 2);
            var day = dateStr.substr(0, dateStr.indexOf('.'));

            return new Date(year, month, day).getTime();
        }

        function _prepareQueries(queries) {
            var result = [];

            for (var k in queries) {
                if (queries.hasOwnProperty(k)) {
                    var queryObj = queries[k];
                    result.push({text: queryObj});
                }
            }

            return result;
        }

        function resolveParam(params, key) {
            var result = {};
            var queries = 'queries';
            var macrosIds = 'macrosIds';

            if ((key.match(/date/) || key.match(/Date/)) && angular.isString(params[key])) {
                result[key] = _parseDate(params[key]);
            } else if (key === queries) {
                result[key] = JSON.stringify(_prepareQueries(params[key]));
            } else if (key === macrosIds) {
                result.macros = params[key].toString();
            } else if (angular.isArray(params[key])) {
                result[key] = params[key].toString();
            } else if (angular.isObject(params[key]) && !angular.isArray(params[key])) {
                result[key] = JSON.stringify(params[key]);
            } else {
                result[key] = params[key];
            }

            return result;
        }

        function _prepareParamsForUrl(params) {


            for (var k in params) {
                if (params.hasOwnProperty(k)) {
                    resolveParam(params, k, result);
                }
            }

            return result;
        }

        function _goToLoadedState(params) {
            $state.go('analytics', params);
        }

        $scope.loadQuery = function () {
            var params = _prepareParamsForUrl($scope.selectedQuery.params);
            _goToLoadedState(params);
            $modalInstance.close();
        };

        (function _init() {
            $scope.queriesList = queriesListData.results;
        })();

    }]
)
;
'use strict';

angular.module('app.pages.analytics.modals.save_query', [])

    .controller('SaveQueryModalCtrl', ['$scope', '$modalInstance', 'AnalyticsFactory', '$location', '$filter', 'MessagesFactory', function ($scope, $modalInstance, AnalyticsFactory, $location, $filter, MessagesFactory) {

        var dateFormat = 'dd.MM.yyyy';

        var _messages = {
            querySuccessSaved: '  ',
            queriesNotAnArrayError: 'Queries list should be an array'
        };

        $scope.query = {
            name: null,
            params: {}
        };

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        function _prepareQueries(queriesList) {
            if (!angular.isArray(queriesList)) return MessagesFactory.throwDeveloperError(_messages.queriesNotAnArrayError);

            var result = {};

            for (var i = 0; i < queriesList.length; i++) {
                var query = queriesList[i];
                query.id = i;
                result[i] = (query.text);
            }

            return result;
        }

        function _prepareForSave(params) {
            var startDate = (params.startDate) ? $filter('date')(params.startDate, dateFormat): null;
            var endDate = (params.endDate) ? $filter('date')(params.endDate, dateFormat): null;
            var queries = (params.queries) ? _prepareQueries(JSON.parse(params.queries)): null;
            var macrosIdsList = params.macros.split(',');

            return {startDate: startDate, endDate: endDate, queries: queries, macrosIds: macrosIdsList};
        }

        function _goToPrevState() {
            $location.href= '/analytics?' + JSON.stringify($location.search());
        }

        $scope.saveQuery = function () {
            var params = _prepareForSave($location.search());

            return AnalyticsFactory.saveQuery($scope.query.name, params).then(function () {
                MessagesFactory.successMsg(_messages.querySuccessSaved);
                _goToPrevState();
                $modalInstance.close();
            });
        };

        (function _init() {
            //
        })();

    }]
)
;
