angular.module('app.pages.cards.modals.edit_card', [])

    .controller('EditCardModalCtrl', ['$scope', '$modalInstance', 'CardFactory', 'cardsData', '$timeout', 'ScriptFactory', 'MacrosFactory', '$sce', 'NgTableUtilsFactory', function ($scope, $modalInstance, CardFactory, cardsData, $timeout, ScriptFactory, MacrosFactory, $sce, NgTableUtilsFactory) {

        $scope.card = {};
        $scope.isValidScript = false;

        $scope.isError = false;

        $scope.editorOptions = {
            lineWrapping: true,
            lineNumbers: true,
            mode: {name: "javascript", json: true}
        };

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        function prepareOnGet(card) {
            if (card && card.data) {
                card.dataPreview = ScriptFactory.stringify(card.data, true);
            }

            return card;
        }

        function prepareForSave(card) {
            card.data = ScriptFactory.removeFormatting(card.dataPreview);

            var pack = JSON.parse(JSON.stringify(card));
            delete pack.dataPreview;

            return pack;
        }

        $scope.update = function () {
            var pack = prepareForSave($scope.card);

            return CardFactory.updateCard(pack).then(function () {
                NgTableUtilsFactory.reload();
                $modalInstance.close();
            });
        };

        function refreshCodeMirrorOnLoad() {
            $timeout(function () {
                $scope.isLoaded = !$scope.isLoaded;
            });
        }

        function isLoadError() {
            if (cardsData.results.length === 0 || cardsData.results.length > 1) return true;

            return false;
        }

        $scope.macrosList = [];

        $scope.searchMacros = function (search) {
            return MacrosFactory.getMacrosByQuery(search).then(function (data) {
                $scope.macrosList = data.results;
            });
        };

        $scope.trustAsHtml = function (value) {
            return $sce.trustAsHtml("" + value);
        };

        $scope.$watch('card.dataPreview', function (value) {
                if (!value) {
                    $scope.isValidScript = false;
                    return;
                }

                ScriptFactory.isValidScript(value).then(function (data) {
                    $scope.isValidScript = data.isValid;
                });
            }, true
        );

        (function init() {
            var card = cardsData.results[0]; //we expect only one card
            $scope.card = prepareOnGet(card);
            $scope.isError = isLoadError();
            refreshCodeMirrorOnLoad();
        })();

    }])
;
