angular.module('app.pages.launch.modals.add_launch', [])

    .controller('AddNewLaunchModalCtrl', ['$scope', '$modalInstance', 'MacrosFactory', 'TagsFactory', 'LaunchFactory', 'ScriptFactory', 'constantMap', '$sce', 'NgTableUtilsFactory', 'macrosData', '$stateParams', function ($scope, $modalInstance, MacrosFactory, TagsFactory, LaunchFactory, ScriptFactory, constantMap, $sce, NgTableUtilsFactory, macrosData, $stateParams) {

        $scope.launchSettings = {};

        $scope.editorOptions = {
            lineWrapping: true,
            lineNumbers: true,
            mode: {name: "javascript", json: true}
        };

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        function _getMacrosIds(macrosList) {
            var result = [];

            for (var i = 0; i < macrosList.length; i++) {
                var macros = macrosList[i];
                result.push(macros.id);
            }

            return result;
        }

        function _getTagIds(tagList) {
            var result = [];

            for (var i = 0; i < tagList.length; i++) {
                var tag = tagList[i];
                result.push(tag.id);
            }

            return result;
        }

        $scope.addLaunch = function () {
            var macrosIds = _getMacrosIds($scope.macros.selectedMacrosList);
            var pack = $scope.launchSettings;

            pack.tag_ids = _getTagIds($scope.tags.selectedTagList);

            return LaunchFactory.addLaunch(macrosIds, pack).then(function () {
                NgTableUtilsFactory.reload();
                $modalInstance.close();
            });
        };

        $scope.macros = {
            selectedMacrosList: {},
            dropdownMacrosList: [],
            searchMacros: function (search) {
                return MacrosFactory.getMacrosByQuery(search).then(function (data) {
                    $scope.macros.dropdownMacrosList = data.results;
                });
            }
        };

        $scope.tags = {
            selectedTagList: {},
            dropdownTagList: [],
            searchTag: function (search) {
                return TagsFactory.getTagByQuery(search).then(function (data) {
                    $scope.tags.dropdownTagList = data.results;
                });
            }
        };

        $scope.trustAsHtml = function (value) {
            return $sce.trustAsHtml("" + value);
        };

        function _getIdsList(obj, field) {
            var result = [];

            for (var k in obj) {
                if (obj.hasOwnProperty(k)) {
                    result.push(obj[k][field]);
                }
            }

            return result;
        }

        $scope.$watch('macros.selectedMacrosList', function (value) {
                if (!value || value.length === 0) return;

                var idField = 'id';
                MacrosFactory.getPlaceholders(_getIdsList($scope.macros.selectedMacrosList, idField)).then(function (data) {
                    $scope.launchSettings.params = data.results;
                });

            }, true
        );

        (function _init() {
            $scope.macros.selectedMacrosList = macrosData.results;
            $scope.LaunchFactory = LaunchFactory;
            $scope.STORAGES = constantMap.STORAGES;
            $scope.ENTITY_RECOGNITION = constantMap.ENTITY_RECOGNITION;
            $scope.ScriptFactory = ScriptFactory;
        })();

    }]
)
;

