angular.module('app.pages.launch.modals.edit_launch', [])

    .controller('EditLaunchModalCtrl', ['$scope', '$modalInstance', 'LaunchFactory', 'TagsFactory', 'launchData', '$timeout', 'ScriptFactory', 'constantMap', '$sce', 'NgTableUtilsFactory', 'MacrosFactory', function ($scope, $modalInstance, LaunchFactory, TagsFactory, launchData, $timeout, ScriptFactory, constantMap, $sce, NgTableUtilsFactory, MacrosFactory) {

        $scope.launch = {};
        $scope.isError = false;
        $scope.isValidScriptParams = false;

        $scope.editorOptions = {
            lineWrapping: true,
            lineNumbers: true,
            mode: {name: "javascript", json: true}
        };

        function _getTagIds(tagList) {
            var result = [];

            for (var i = 0; i < tagList.length; i++) {
                var tag = tagList[i];
                result.push(tag.id);
            }

            return result;
        }

        function getPlaceholders(launch) {
            return MacrosFactory.getPlaceholders(launch.macros.id);
        }

        function arrayDiff(fromArr, toArr) {
            return fromArr.filter(function (i) {
                return toArr.indexOf(i) < 0;
            });
        }

        function mergeParamsAndPlaceholders(launch, placeholdersGroupArr) {
            var diffArr;

            if (placeholdersGroupArr && placeholdersGroupArr[0]) {
                var placeholdersArr = Object.keys(placeholdersGroupArr[0].placeholders);
                var paramsArr = Object.keys(launch.params);
                diffArr = arrayDiff(placeholdersArr, paramsArr);
            }

            for (var i = 0; i < diffArr.length; i++) {
                var newPlaceholder = diffArr[i];
                launch.params[newPlaceholder] = null;
            }

        }

        function prepareLaunchOnGet(launch) {
            getPlaceholders(launch).then(function (data) {
                mergeParamsAndPlaceholders(launch, data.results);
            });

            return launch;
        }

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        $scope.update = function () {
            var pack = $scope.launch;
            pack.tag_ids = _getTagIds($scope.tags.selectedTagList);

            return LaunchFactory.updateLaunch(pack).then(function () {
                NgTableUtilsFactory.reload();
                $modalInstance.close();
            });
        };

        $scope.tags = {
            selectedTagList: {},
            dropdownTagList: [],
            searchTag: function (search) {
                return TagsFactory.getTagByQuery(search).then(function (data) {
                    $scope.tags.dropdownTagList = data.results;
                });
            }
        };

        function refreshCodeMirrorOnLoad() {
            $timeout(function () {
                $scope.isLoaded = !$scope.isLoaded;
            });
        }

        function isLoadError() {
            if (!$scope.launch || angular.equals({}, $scope.launch)) return true;
            if (launchData.results > 1) return true;

            return false;
        }

        $scope.trustAsHtml = function (value) {
            return $sce.trustAsHtml("" + value);
        };

        (function _init() {
            var launch = launchData.results[0];
            $scope.ScriptFactory = ScriptFactory;
            $scope.STORAGES = constantMap.STORAGES;
            $scope.ENTITY_RECOGNITION = constantMap.ENTITY_RECOGNITION;
            $scope.launch = prepareLaunchOnGet(launch);
            $scope.isError = isLoadError();
            $scope.tags.selectedTagList = $scope.launch.tags;

            if (!$scope.isError) {
                LaunchFactory.assignLinkedMacrosToLaunchList([$scope.launch]);
            }

            refreshCodeMirrorOnLoad();
        })();
    }])
;

