angular.module('app.pages.macros.modals.add_macros', [])

    .controller('AddMacrosModalCtrl', ['$scope', '$modalInstance', '$document', 'MacrosFactory', 'ScriptFactory', 'constantMap', '$timeout', '$location', '$sce', 'MessagesFactory', 'NgTableUtilsFactory', function ($scope, $modalInstance, $document, MacrosFactory, ScriptFactory, constantMap, $timeout, $location, $sce, MessagesFactory, NgTableUtilsFactory) {

        $scope.editorOptions = {
            lineWrapping: true,
            lineNumbers: true,
            mode: {name: "javascript", json: true}
        };

        var _messages = {
            macrosSuccessfulSaved: '  ',
            macrosSaveFailed: '  ',
            errorScriptParsing: '   ( json?)'
        };

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        $scope.macros = {};
        $scope.isValidScript = false;
        $scope.dropdownMacrosList = [];

        $scope.searchMacros = function (search) {
            return MacrosFactory.getMacrosByQuery(search).then(function (data) {
                $scope.dropdownMacrosList = data.results;
            });
        };

        function deleteEmptyValues(entity) {
            for (var field in entity) {
                if (entity.hasOwnProperty(field)) {
                    if (entity[field] === '' || entity[field] === ' ' || !entity[field]) {
                        delete entity[field];
                    }
                }
            }

            return entity;
        }

        function prepareForSave(macros) {
            macros.script = ScriptFactory.removeFormatting(macros.script_preview);

            var pack = JSON.parse(JSON.stringify(macros));
            delete pack.script_preview;

            deleteEmptyValues(pack);

            return pack;
        }

        function getUrlRefer() {
            $scope.macros.url = $document.referrer;
        }

        function refreshCodeMirrorOnLoad() {
            $timeout(function () {
                $scope.isLoaded = !$scope.isLoaded;
            });
        }

        $scope.add = function () {
            var pack = prepareForSave($scope.macros);

            return MacrosFactory.addMacros(pack).then(function () {
                NgTableUtilsFactory.reload();
                $modalInstance.close();
            });
        };

        function parseUrlParams() {
            var path = $location.$$path;
            var hash = $location.$$url;

            if (path === hash) return null;

            var params = decodeURIComponent(hash.replace(path + '?', ''));
            var paramsArr = params.split("&");

            var indexRegExp = new RegExp(/^\d/);
            var paramNameRegExp = new RegExp(/\[\w*\]/);

            var result = [];

            for (var i = 0; i < paramsArr.length; i++) {
                var param = paramsArr[i];
                var paramIndex = indexRegExp.exec(param)[0]; // 0
                var paramName = paramNameRegExp.exec(param)[0];
                paramName = paramName.substr(1, paramName.length - 2); // 'xpath'
                var paramValue = (param).substr(param.indexOf('=') + 1); // "//*[@id='mw-content-text']/*[1]"

                if (!result[paramIndex]) {
                    result[paramIndex] = {};
                }

                result[paramIndex][paramName] = paramValue;
            }

            return result;
        }

        function getMacrosFromParams() {
            var script = parseUrlParams();
            if (script) {
                if (ScriptFactory.isCanStringify(script)) {
                    $scope.macros.script_preview = ScriptFactory.stringify(script, true);
                } else {
                    MessagesFactory.errorMsg(_messages.errorScriptParsing);
                    MessagesFactory.throwDeveloperError(_messages.errorScriptParsing);
                }
            }
        }

        $scope.trustAsHtml = function (value) {
            return $sce.trustAsHtml("" + value);
        };

        $scope.resetSpiderParams = function resetSpiderParams() {
            $scope.macros.spider_params = {};
        };

        $scope.$watch('macros.script_preview', function (value) {
                if (!value) {
                    $scope.isValidScript = false;
                    return;
                }

                ScriptFactory.isValidScript(value, true).then(function (data) {
                    $scope.isValidScript = data.isValid;
                });
            }, true
        );

        function getObjectFromArr(value, arr) {
            var obj;
            for (var i = 0; i < arr.length; i++) {
                obj = arr[i];
                if (obj.value === value) {
                    return obj;
                }
            }

            return obj;
        }

        function _initSelects() {
            $scope.macros.preprocessing = getObjectFromArr('', $scope.PREPROCESSING).value;
            $scope.macros.spider = getObjectFromArr('grab', $scope.SPIDERS).value;
        }

        (function init() {
            $scope.SPIDER_PARAMS = constantMap.SPIDER_PARAMS;
            $scope.PREPROCESSING = constantMap.PREPROCESSING;
            $scope.SPIDERS = constantMap.SPIDERS;
            _initSelects();
            getMacrosFromParams();
            getUrlRefer();
            refreshCodeMirrorOnLoad();
        })();
    }])
;
