angular.module('app.pages.sequences.modals.add_sequence', [])

    .controller('AddSequenceModalCtrl', ['$scope', '$modalInstance', 'TagsFactory', 'SequencesFactory', 'NgTableUtilsFactory', '$sce', function ($scope, $modalInstance, TagsFactory, SequencesFactory, NgTableUtilsFactory, $sce) {

        function _getTagIds(tagList) {
            var result = [];

            for (var i = 0; i < tagList.length; i++) {
                var tag = tagList[i];
                result.push(tag.id);
            }

            return result;
        }

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        $scope.add = function () {
            $scope.sequence.tag_ids = _getTagIds($scope.tags.selectedTagList);
            return SequencesFactory.addSequence($scope.sequence).then(function () {
                NgTableUtilsFactory.reload();
                $modalInstance.close();
            });
        };

        $scope.tags = {
            selectedTagList: {},
            dropdownTagList: [],
            searchTag: function (search) {
                return TagsFactory.getTagByQuery(search).then(function (data) {
                    $scope.tags.dropdownTagList = data.results;
                });
            }
        };

        $scope.trustAsHtml = function (value) {
            return $sce.trustAsHtml("" + value);
        };

        (function _init() {
            $scope.sequence = {body: []};
            if (window.localStorage) {
                if (!localStorage.getItem('firstLoad')) {
                    localStorage['firstLoad'] = true;
                    window.location.reload();
                }
                else {
                    localStorage.removeItem('firstLoad');
                }
            }
        })();

        window.onload = function(){
            if(window.File && window.FileList) {

                document.getElementById('btn-open').onclick = function() {
                    if ('FileReader' in window) {
                        document.getElementById('file-browser').click();
                    } else {
                        alert('Your browser does not support the HTML5 FileReader.');
                    }
                };

                document.getElementById('file-browser').onchange = function(event) {
                    var fileToLoad = event.target.files[0];

                    if (fileToLoad !== undefined) {
                        var reader = new FileReader();
                        reader.onload = function(fileLoadedEvent) {
                            $scope.sequence['body'] = fileLoadedEvent.target.result;
                            document.getElementById('sequence_body').click();
                            //document.getElementById('sequence_body').value = textFromFileLoaded;
                        };
                        reader.readAsText(fileToLoad, "UTF-8");
                    }
                };
            }
            else
            {
                console.log("Your browser does not support File API");
            }
        }
    }])
;

