angular.module('app.pages.users.modals.edit_user', [])
    .controller('EditUsersPageCtrl', ['$modalInstance', '$scope', '$state', 'UserFactory', '$filter', '$stateParams', 'userData', 'DatesFactory', 'NgTableUtilsFactory', 'constantMap', function ($modalInstance, $scope, $state, UserFactory, $filter, $stateParams, userData, DatesFactory, NgTableUtilsFactory, constantMap) {

        $scope.isError = false;

        $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
        };

        $scope.save = function () {
            $scope.user.user_id = $scope.user.id;
            if ($scope.user.newPassword && ($scope.user.newPassword === $scope.user.newPasswordConf)) {
                $scope.user.password = $scope.user.newPassword;
                delete $scope.user.newPassword;
                delete $scope.user.newPasswordConf;
            }

            return makeUpdate($scope.user);
        };

        function makeUpdate(user) {
            return UserFactory.updateUser(user).then(function () {
                NgTableUtilsFactory.reload();
                $modalInstance.close();
            });
        }

        $scope.selectedRoles = function () {
            $scope.user.roles = $filter('filter')($scope.roles, {checked: true}).map(function (el) {
                return el.id;
            });
        };

        $scope.isNewPasswordError = function () {
            if (!$scope.user.newPassword || $scope.user.newPassword !== "") return false;
            if ($scope.user.newPassword === $scope.user.newPasswordConf) return false;

            return true;
        };

        function isLoadError() {
            if (userData.results.length === 0 || userData.results.length > 1) return true;

            return false;
        }

        $scope.refreshData = function (m, y) {
            DatesFactory.getCalendarData(m, y).then(function (data) {
                $scope.calendarData = data;
            });
        };

        (function init() {
            $scope.update = true;
            $scope.user = userData.results[0];
            $scope.isError = isLoadError();
            $scope.USER_ROLES = constantMap.USER_ROLES;
            $scope.DatesFactory = DatesFactory;
        })();

    }])
;
